// Variables
@import "variables";

// Font Awesome
@import "font-awesome/font-awesome";
html,
body {
  height: 100%;
}
.box {
  width: 450px;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 0 0 3px 3px;
  overflow: hidden;
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  //Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
  box-shadow: 0 10px 10px $color_7, 0 6px 3px $color_8;
}
.header {
  background-color: #1e1e1e;
  padding: 30px 30px 40px;
  border-radius: 3px 3px 0 0;
  text-align: center;
}
.header__step {
  font-weight: 300;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1.1px;
  margin: 0 0 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  //Instead of the line below you could use @include user-select($select)
  user-select: none;
  color: $color_10;
}
.header__title {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  //Instead of the line below you could use @include user-select($select)
  user-select: none;
  color: #ffffff;
  font-weight: 400;
  margin: 0 0 15px;
  border: none;
}
.step {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: row-reverse;
  -ms-flex-direction: row-reverse;
  flex-direction: row-reverse;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  margin-top: -20px;
}
.step__divider {
  background-color: $color_11;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  //Instead of the line below you could use @include user-select($select)
  user-select: none;
  width: 60px;
  height: 3px;
  &:first-child {
    -webkit-flex: 1 0 auto;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
  }
  &:last-child {
    -webkit-flex: 1 0 auto;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
  }
}
.step__icon {
  background-color: $color_11;
  font-style: normal;
  width: 40px;
  height: 40px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 50%;
  color: $color_10;
  &.welcome:before {
    content: "\f144";
    font-family: $font_10;
  }
  &.requirements:before {
    content: "\f127";
    font-family: $font_10;
  }
  &.permissions:before {
    content: "\f296";
    font-family: $font_10;
  }
  &.database:before {
    content: "\f454";
    font-family: $font_10;
  }
  &.update:before {
    content: "\f2bf";
    font-family: $font_10;
  }
}
.main {
  margin-top: -20px;
  background-color: $color_10;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 0 0 3px 3px;
  padding: 40px 40px 30px;
}
.buttons {
  text-align: center;
  .button {
    margin: 0.5em;
  }
}
.buttons--right {
  text-align: right;
}
.button {
  display: inline-block;
  background-color: #3886d9;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 4px;
  padding: 7px 20px;
  color: #ffffff;
  text-decoration: none;
  outline: none;
  border: none;
  cursor: pointer;
  &:hover {
    color: #ffffff;
    background-color: #2673c5;
  }
}
.button--light {
  padding: 3px 16px;
  font-size: 16px;
  border-top: 1px solid $color_17;
  color: $color_18;
  background: $color_10;
  &:hover {
    color: $color_18;
    background: $color_10;
    //Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
    box-shadow: 0 3px 3px $color_19, 0 3px 3px $color_8;
  }
}
.list {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
  margin: 20px 0 35px;
  border: 1px solid $color_14;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 2px;
  .list__item.list__title {
    background: $color_14;
    &.success {
      span {
        color: $color_31;
      }
      .fa:before {
        color: $color_31;
      }
    }
    &.error {
      span {
        color: $color_24;
      }
      .fa:before {
        color: $color_24;
      }
    }
  }
}
.list__item {
  position: relative;
  overflow: hidden;
  padding: 7px 20px;
  border-bottom: 1px solid $color_14;
  &:first-letter {
    text-transform: uppercase;
  }
  &:last-child {
    border-bottom: none;
  }
  .fa.row-icon:before {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 7px 20px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
  }
  &.success .fa:before {
    color: $color_20;
  }
  &.error .fa:before {
    color: $color_21;
  }
}
.list__item--permissions {
  &:before {
    content: "" !important;
  }
  span {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 7px 20px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: $color_22;
    font-weight: 700;
    font-size: 16px;
    &:before {
      margin-right: 7px;
      font-weight: 400;
    }
  }
  &.success i:before {
    color: $color_20;
    vertical-align: 1px;
  }
  &.error i:before {
    color: $color_21;
    vertical-align: 1px;
  }
}
.textarea {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;
  font-size: 14px;
  line-height: 25px;
  height: 150px;
  outline: none;
  border: 1px solid $color_23;
  ~ .button {
    margin-bottom: 35px;
  }
}

.has-error {
  color: $color_24;
  input {
    color: $color_25;
    border: 1px solid $color_26;
  }
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.tabs {
  padding: 0;
  .tab-input {
    display: none;
    &:checked + .tab-label {
      background-color: $color_10;
      color: $color_29;
    }
  }
  .tab-label {
    color: $color_28;
    cursor: pointer;
    float: left;
    padding: 1em;
    text-align: center;
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.1s ease-in-out;
    &:hover {
      color: $color_29;
    }
  }
  .tabs-wrap {
    clear: both;
  }
  .tab {
    display: none;
    > *:last-child {
      margin-bottom: 0;
    }
  }
}
.buttons-container {
  min-height: 37px;
  margin: 1em 0 0;
}
.block {
  //Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
  box-shadow: 0 3px 1px $color_32;
  input[type="radio"] {
    width: 100%;
    display: none;
    + label {
      background: $color_33;
      color: $color_10;
      padding-top: 5px;
      -webkit-transition: all 0.1s ease-in-out;
      -moz-transition: all 0.1s ease-in-out;
      -o-transition: all 0.1s ease-in-out;
      //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
      transition: all 0.1s ease-in-out;
      &:hover {
        background: $color_34;
        color: $color_10;
        padding-top: 5px;
      }
    }
    &:checked {
      + label {
        background-color: $color_35;
      }
      ~ .info {
        height: 200px;
        overflow-y: auto;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
        transition: all 0.3s ease-in-out;
      }
    }
    ~ .info > div {
      padding-top: 15px;
    }
  }
  label {
    width: 450px;
    max-width: 100%;
    cursor: pointer;
  }
  span {
    font-family: $font_5;
    font-weight: 700;
    display: block;
    padding: 10px 12px 12px 15px;
    margin: 0;
    cursor: pointer;
  }
}
.info {
  background: $color_10;
  color: $color_18;
  width: 100%;
  height: 0;
  line-height: 2;
  padding-left: 15px;
  padding-right: 15px;
  display: block;
  overflow: hidden;
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
  transition: 0.3s ease-out;
}
::selection {
  background: $color_18;
  color: $color_10;
}
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px $color_36;
  -webkit-border-radius: 0;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 0;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 0;
  background: $color_37;
  -webkit-box-shadow: inset 0 0 6px $color_38;
}
.margin-bottom-1 {
  margin-bottom: 1em;
}
.margin-bottom-2 {
  margin-bottom: 1em;
}
.margin-top-1 {
  margin-top: 1em;
}
.margin-top-2 {
  margin-top: 1em;
}
.alert {
  margin: 0 0 10px;
  font-size: 1.1em;
  background-color: $color_22;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 3px;
  padding: 10px;
  position: relative;
  &.alert-danger {
    background: $color_24;
    color: $color_39;
    padding: 10px 20px 15px;
    h4 {
      color: $color_39;
      margin: 0;
    }
    ul {
      margin: 0;
    }
  }
  .close {
    width: 25px;
    height: 25px;
    padding: 0;
    margin: 0;
    //Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
    box-shadow: none;
    border: 2px solid $color_26;
    outline: none;
    float: right;
    //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
    border-radius: 50%;
    position: absolute;
    right: 0;
    top: 0;
    background-color: transparent;
    cursor: pointer;
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.1s ease-in-out;
    &:hover {
      background-color: $color_39;
      color: $color_24;
    }
  }
}
svg:not(:root) {
  overflow: hidden;
}
.step__item.active .step__icon,
.step__item.active ~ .step__divider,
.step__item.active ~ .step__item .step__icon {
  background-color: #b31923;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}
.step__item.active .step__icon:hover,
.step__item.active ~ .step__item .step__icon:hover {
  background-color: #6e0e14;
}
.form-group.has-error {
  select {
    border-color: $color_24;
  }
  textarea {
    border-color: $color_24;
  }
  input[type="text"] {
    border-color: $color_24;
  }
  input[type="password"] {
    border-color: $color_24;
  }
  input[type="url"] {
    border-color: $color_24;
  }
  input[type="number"] {
    border-color: $color_24;
  }
  .error-block {
    display: block;
    width: 100%;
    font-size: 0.9em;
    color: $color_24;
    font-weight: 500;
  }
}
.tabs-full .tab-label {
  display: table-cell;
  float: none;
  width: 1%;
}
#tab1:checked ~ .tabs-wrap #tab1content {
  display: block;
}
#tab2:checked ~ .tabs-wrap #tab2content {
  display: block;
}
#tab3:checked ~ .tabs-wrap #tab3content {
  display: block;
}
#tab4:checked ~ .tabs-wrap #tab4content {
  display: block;
}
#tab5:checked ~ .tabs-wrap #tab5content {
  display: block;
}
.github img {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}
#tab3content .block {
  &:first-child {
    //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
    border-radius: 3px 3px 0 0;
  }
  &:last-child {
    //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
    border-radius: 0 0 3px 3px;
  }
}



